:root {
  font-family: "inter";
  font-size: 18px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "kreon";
}

p, span {
  font-family: "inter";
}

#wrapper {
  background: linear-gradient(90deg, rgb(247, 247, 247) 0%, rgb(247, 247, 247) 50%, rgb(255, 255, 255) 50%, rgb(255, 255, 255) 100%);
}

nav#header {
  background: rgba(0, 0, 0, 0) !important;
}
nav#header .navbar-brand img {
  max-height: 64px;
  height: 100%;
}
nav#header .navbar-nav {
  justify-content: end;
  column-gap: 45px;
}
nav#header .navbar-nav a.nav-link {
  text-transform: uppercase;
  font-size: 15px;
  font-weight: 500;
  padding-left: 0;
  padding-right: 0;
}
nav#header .languages p, nav#header .languages span {
  font-family: "kreon";
  font-size: 15px;
  font-weight: 400;
  line-height: 19px;
}

a {
  text-decoration: none;
}

.home_page_section_1 .content {
  padding-top: 200px;
  padding-bottom: 50px;
}
@media (max-width: 1200px) {
  .home_page_section_1 .content {
    padding-top: 100px;
  }
}
@media (max-width: 1200px) {
  .home_page_section_1 .content {
    padding-top: 50px;
  }
}
@media (max-width: 480px) {
  .home_page_section_1 .content {
    padding-top: 20px;
    padding-bottom: 20px;
  }
}

.offset-negative-lg-1 {
  margin-left: 8.33333333%;
}

.rounded-number {
  display: block;
  width: 30px;
  height: 30px;
  aspect-ratio: 1/1;
  border-radius: 30px;
  line-height: 30px;
  text-align: center;
}

.btn {
  font-size: 16px;
  padding: 15px 30px;
  font-weight: 700;
}

.home_page_section_2 ul.splide__pagination {
  justify-content: start;
}